import { Component, OnInit,Inject } from '@angular/core';
import {Router,ActivatedRoute} from '@angular/router';
import { UserServiceService } from '../user-service.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { fromEventPattern } from 'rxjs';
@Component({
  selector: 'app-user-dashboard',
  templateUrl: './user-dashboard.component.html',
  styleUrls: ['./user-dashboard.component.scss']
})
export class UserDashboardComponent implements OnInit {

  step = 0;
  isValidQuestion : boolean;
  loggedInUsermail : string;
  loggedInUserfname : string;
  searchText : string;
  questionTitle : string ;
  qList : any;
  dialogBoxMessage : string;
  questionDescription : string;
  userProfile : UserProfile[];
  constructor(public dialog: MatDialog,private route: ActivatedRoute,private router:Router,private userservice:UserServiceService) {
    this.questionTitle="";
    this.searchText="";
    this.isValidQuestion = false;
    this.qList = [];
    if(localStorage != undefined){
      this.loggedInUserfname = localStorage.getItem(localStorage.key(0));
      this.loggedInUsermail = localStorage.key(0);
    }
   }

  ngOnInit() {
    if(localStorage == undefined || localStorage.getItem(localStorage.key(0)) == null || localStorage.getItem(localStorage.key(0)) == undefined)
    this.router.navigateByUrl('/');
    this.getUserProfile();
    this.userservice.getQuestionList().subscribe(data=>{this.qList.push(data.json())});
  }
  getUserProfile(){
    this.userProfile=[];
     this.userservice.getProfile(this.loggedInUsermail).subscribe(data=>{this.userProfile.push(data.json())});
}
  logout(){
    if(localStorage == undefined || localStorage.getItem(localStorage.key(0)) == null || localStorage.getItem(localStorage.key(0)) == undefined)
    this.router.navigateByUrl('/');
    else{
    localStorage.removeItem(this.loggedInUsermail);
    localStorage.clear();
    this.userservice.logoutUser(this.loggedInUsermail).subscribe(data=>{
      if(data.json().status==200)
      this.router.navigateByUrl('/');
    });
  }
  }
  setStep(index: number) {
    this.step = index;
  }
  cancel(){
    this.questionDescription="";
    this.questionTitle="";
    this.step++;
  }
  validateQFields(){
    if(this.questionTitle!=null || this.questionTitle != undefined)
    this.isValidQuestion=true;
  }
  postQuestion(){
    if( localStorage != undefined){
      this.router.navigateByUrl('/');
    }
    else{
    let question={"first_name":this.loggedInUserfname,"email":this.loggedInUsermail,"createdDateTime":Date.now(),"questionTitle":this.questionTitle,"questionDescription":this.questionDescription};
    this.userservice.saveQuestion(question).subscribe(data=>{
      if(data.json().status=201){
      const dialogRef = this.dialog.open(DialogBox, {
        width: '350px',
        data: { message:this.dialogBoxMessage}
      });
  
      dialogRef.afterClosed().subscribe(result => {
        this.cancel();
      });
    }
    });
    }
  }
}

@Component({
  selector: 'dialog-box',
  templateUrl: 'dialog-box.html',
})
export class DialogBox{

  constructor(
    public dialogRef: MatDialogRef<DialogBox>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}

export interface DialogData {
  message:string;
}

export interface UserProfile {
  first_name : string;
  last_name : string;
  email : string ;
  mobile : string ;
  gender : string ;
}
