import {Injectable} from '@angular/core';
import {Http,Headers} from '@angular/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserServiceService {

  constructor(private http : Http) { }

  getUsers():Observable<any>{
    return this.http.get('/api/retrieve');
  }
  verifyUser(loginUser):Observable<any>{
    var headers= new Headers();
    headers.append('content-Type','application/JSON');
    return this.http.post('/api/verify',loginUser,{headers:headers});
  }
  addUser(newUser):Observable<any>{
    var headers= new Headers();
    headers.append('content-Type','application/JSON');
    return this.http.post('/api/add',newUser,{headers:headers});
  }

  deleteUser(id):Observable<any>{
    return this.http.delete('/api/delete/'+id);
  }
  authenticateUser():Observable<any>{
    let data: any;
    return this.http.get('/api/oauth2');

  }
  saveQuestion(quest):Observable<any>{
    var headers= new Headers();
    headers.append('content-Type','application/JSON');
    return this.http.post('/api/savequestion',quest,{headers:headers});
  }
  getQuestionList() : Observable<any>{
    return this.http.get('/api/qList');
  }
  getProfile(email):Observable<any>{
    return this.http.get('/api/profile/'+email);
  }
  logoutUser(email):Observable<any>{
    return this.http.get('/api/logout/'+email);
  }
}
