import { Component, OnInit } from '@angular/core';
import { UserServiceService } from '../user-service.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss','../animate.css']
})
export class UsersComponent implements OnInit {
  

  users : any;
  fname : String;
  lname : String;
  email : String;
  mobile : Number;
  gender : String;

  constructor(private userservice : UserServiceService,private router: Router) { 
    this.fname="";
    this.lname="";
    this.email="";
    this.gender="";

  }

  ngOnInit() {
  }
  get(){
    this.users=[];
    this.userservice.getUsers().subscribe(data=>{this.users.push(data.json());})

  }

  add(){
    let newUser={"firstname":this.fname,"lastname":this.lname,"email":this.email,"gender":this.gender,"mobile":this.mobile};
    this.userservice.addUser(newUser).subscribe(data=>{});
    this.get();
  }
  delete(id){
    this.userservice.deleteUser(id).subscribe();
    this.get();
  }
  btnClick= function () {
    this.router.navigateByUrl('/', { skipLocationChange: true });
};
}

