import { Component, OnInit } from '@angular/core';
import {UserServiceService} from '../user-service.service';
import {Router} from '@angular/router';
import * as EmailValidator from 'email-validator';
import {
  AuthService,
  GoogleLoginProvider
} from 'angular-6-social-login';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  users : any;
  fname : string;
  lname : string;
  email : string;
  mobile : Number;
  gender : string;
  password : string;
  consent : boolean;
  isExists : Boolean;
  fnamehint: string;
  lnamehint: string;
  passwordhint : string ;
  mobilehint: string;
  emailhint: string;
  genderhint: string;
  msg: string = "";
  flag: number = 1;
  isValid: boolean;
  types : String[]=['Male','Female'];

  constructor(private userservice : UserServiceService,private router: Router,private socialAuthService: AuthService) { 
    this.fname="";
    this.lname="";
    this.email="";
    this.password="";
    this.isValid = false;

  }

  ngOnInit() {
    this.get();
  }
  get(){
    this.users=[];
    this.userservice.getUsers().subscribe(data=>{this.users.push(data.json());})

  }

  add(){
    for(let i of this.users){
      for(let j of i){
      if(this.email.match(j.email)){
      this.isExists=true;
      break;
      }
      else
      this.isExists=false;
    }
    }
    if(!this.isExists){

    let newUser={"firstname":this.fname,"lastname":this.lname,"email":this.email,"password":this.password,"gender":this.gender,"mobile":this.mobile};
    this.userservice.addUser(newUser).subscribe(data=>{});
    window.alert('Details added successfully');
    this.router.navigateByUrl('/', { skipLocationChange: true });
    }
    else{
      window.alert('Email already exists..Please try with different email');
    }
    
  }
  delete(id){
    this.userservice.deleteUser(id).subscribe();
    this.get();
  }
  login(){
    this.router.navigateByUrl('/', { skipLocationChange: true });

  }
  getlist(){
    this.router.navigateByUrl('ui/users', { skipLocationChange: true });
  }
  public socialSignIn(socialPlatform : string) {
    let socialPlatformProvider;
    if(socialPlatform == "google"){
      socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
    } 
    this.socialAuthService.signIn(socialPlatformProvider).then(
      (userData) => {
        let fullname;
        fullname=userData.name.split(" ");
        this.fname=fullname[0];
        this.lname=fullname[1];
        this.email=userData.email;
        // Now sign-in with userData
        // ...
            
      }
    );
  }
  validateDetails() {

    if (!this.validatefName()) {
      this.flag = 0;
    }
    else if (!this.validatelName()) {
      this.flag = 0;
    }
    else if (!this.validatePassword()){
      this.flag =0 ;
    }
    else if (!this.validateEmail()) {
      this.flag = 0;
    }
    else if (!this.validateMobile()) {
      this.flag = 0;
    }
    else {
      this.flag = 1;
    }
    if (this.flag == 1) {
      this.isValid = true;
    }
    else
      this.isValid = false;
  };
  validatefName(): boolean {
    if (this.fname.length < 3 || this.fname.length > 20) {
      this.flag = 0;
      this.fnamehint = "FirstName should be atleast 3 characters";
      this.consent = false;
      return false;
    }
    this.fnamehint = "";
    return true;
  }
  validatelName(): boolean {
    if (this.lname.length < 3 || this.lname.length > 20) {
      this.flag = 0;
      this.lnamehint = "LastName should be atleast 3 characters";
      this.consent = false;
      return false;
    }
    this.lnamehint = "";
    return true;
  }
  keyPress(event:any){
    const pattern = /[0-9\+\-\ ]/;

    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  validateMobile():boolean {
    if (this.mobile==undefined||this.mobile < 6000000000 || this.mobile > 9999999999) {
      this.flag = 0;
      this.mobilehint = "Mobile number should be 10 numbers";
      this.consent = false;
      return false;
    }
    this.mobilehint = "";
    return true;

  }
  validatePassword():boolean{
    if(!this.password.match('(?=.*[A-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})'))
    {
      this.flag=0;
      this.passwordhint='Password should contain atleast 8 characters,a uppercase letter,a lowercase letter and a special character';
      this.consent = false;
      return false;
      }
      this.passwordhint='';
      return true;
  }
  validateEmail(): boolean {
    if(!EmailValidator.validate(this.email))
    {
    this.flag=0;
    this.emailhint='Email should be of valid format(example@domain.com)';
    this.consent = false;
    return false;
    }
    this.emailhint='';
    return true;
  }
}

