import { Component, OnInit } from '@angular/core';
import {UserServiceService} from '../user-service.service';
import {Router,ActivatedRoute} from '@angular/router';
import * as EmailValidator from 'email-validator';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

 
  loggedInUser:any
  users : any;
  email : string;
  password : string;
  isExists : Boolean;
  emailhint : string;
  Userhint : string;
  passwordhint : string;
  flag: number = 1;
  isValid: boolean;
  types : String[]=['Male','Female'];

  constructor(private userservice : UserServiceService,private router: Router) { 
    this.email="";
    this.password="";
    this.emailhint="";
    this.passwordhint="";
    this.Userhint="";
    this.isValid = false;
    
  }

  ngOnInit() {
   this.loggedInUser=[];
   if(localStorage != undefined ){
     let len = localStorage.length;
     if(len > 0){
       let getSession = localStorage.getItem(localStorage.key(0));
     if( getSession != null || getSession != undefined){
      this.router.navigate(['ui/dashboard']);
     }
     }
   }
  }
  login(){
    this.Userhint="";
    let newUser={"email":this.email,"password":this.password};
    this.userservice.verifyUser(newUser).subscribe(
      data=>{
        if(data.status==403){
          this.Userhint = "Your account is not verified.";
        }
        else if(data.status == 404){
          this.Userhint = "Account not found.";
        }
        else{
        this.loggedInUser.push(data.json());
        localStorage.setItem(this.loggedInUser[0].email,this.loggedInUser[0].firstname);
      this.router.navigateByUrl('ui/dashboard')}
        },
      error=>{
      if(error.status==404||error.status==403)
      this.Userhint="Your Credentials are invalid!!";
      });
  }
  signup(){
    this.router.navigateByUrl('ui/signup', { skipLocationChange: true });
  }
  validateDetails() {

    if (!this.validateEmail()) {
      this.flag = 0;
    }
    else if (!this.validatePassword()){
      this.flag =0 ;
    }
    else {
      this.flag = 1;
    }
    if (this.flag == 1) {
      this.isValid = true;
    }
    else
      this.isValid = false;
  };
  validateEmail(): boolean {
    if(!EmailValidator.validate(this.email))
    {
    this.flag=0;
    this.emailhint='Email should be of valid format(example@domain.com)';
    return false;
    }
    this.emailhint='';
    return true;
  }
  validatePassword(): boolean{
    if(this.password==""){
      this.flag=0;
      this.passwordhint= "Password cannot be empty";
      return false;
    }
    else{
      this.passwordhint="";
      return true;
    }
  }

  }
