import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {HomeComponent} from './home/home.component';
import {UsersComponent} from './users/users.component';
import { RouterModule, Routes } from '@angular/router';
import {LoginComponent} from './login/login.component';
import {NotFoundComponent} from './not-found/not-found.component';
import { from } from 'rxjs';
import { UserDashboardComponent } from './user-dashboard/user-dashboard.component';

const routes: Routes = [

  { path: 'ui/users', component: UsersComponent },
  { path: '', component : LoginComponent },
  { path : 'ui/signup', component: HomeComponent},
  { path : 'ui/dashboard', component : UserDashboardComponent},
  { path : 'ui/*' , component : NotFoundComponent}
];
@NgModule({
  imports: [
    RouterModule.forRoot(routes),CommonModule
  ],
    exports: [ RouterModule ],
  declarations: []
})

export class AppRoutingModule {
  
 }
 